      var vm = this;
      var dateString = vm[key];
      var regex = /^(0?[1-9]|[12][0-9]|3[01])[\/\-](0?[1-9]|1[012])[\/\-]\d{4}$/;
      var regex2 = /^(0?[1-9]|[12][0-9]|3[01])[\/\-](0?[1-9]|1[012])[\/\-]\d{2}$/;

      if (!regex.test(dateString) && !regex2.test(dateString)) {
        vm[key] = '';
      } else {
        var date = vm[key].split("/");
        var day = date[0];
        var month = date[1];

        if (day > 31) {
          vm[key] = '';
        } else if (month > 12) {
          vm[key] = '';
        } else if (date[2].length == 2) {
          vm[key] = day + '/' + month + '/20' + date[2];
        }
      }
